<template>
  <v-container>
    <v-simple-table fixed-header>
      <template v-slot:default>
        <thead>
          <tr>
            <th class="text-center text-h6">标题</th>
            <th class="text-center text-h6">作者</th>
            <th class="text-center text-h7">查看/回复</th>
            <th class="text-center text-h6">最后更新</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="item in topics"
            :key="item.id"
            class="showlink"
            @click="showDetail(item.id)"
          >
            <td class="text-h6">{{ item.title }}</td>
            <td width="200px">
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-avatar size="30" color="amber" v-bind="attrs" v-on="on">
                    <img
                      :src="item.createdBy.avatar"
                      :alt="item.createdBy.username"
                      v-if="item.createdBy.avatar"
                    />
                    <span v-else  class="white--text text-h5">
                      {{ item.createdBy.username[0].toUpperCase() }}</span
                    >
                  </v-avatar>
                </template>
                <span>{{ item.createdBy.username }}</span>
              </v-tooltip>
              {{ item.createdBy.username }}
              <br />
              {{ item.createdAt }}
            </td>
            <td width="100px">{{ item.views }} / {{ item.reply }}</td>
            <td width="200px">
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-avatar size="30" color="blue" v-bind="attrs" v-on="on">
                    <img
                      :src="item.lastEditor.avatar"
                      v-if="item.lastEditor.avatar"
                    />
                    <span v-else class="white--text text-h5">
                      {{ item.lastEditor.username[0].toUpperCase() }}
                    </span>
                  </v-avatar>
                </template>
                <span>{{ item.lastEditor.username }}</span>
              </v-tooltip>
              {{ item.lastEditor.username }}
              <br />
              {{ item.updatedAt }}
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
  </v-container>
</template>

<script>
import moment from "moment"; //日期处理类库
import TopicService from "@/service/topic_service.js";
export default {
  name: "home-board",
  data: () => ({
    page: 1, // 当前页
    pageSize: 10, // 页大小
    topics: [],
    //
  }),
  mounted() {
    this.getTopics(this.$route.params.id);
    //
  },
  methods: {
    getTopics(boardId) {
      let skip = (this.page - 1) * this.pageSize;
      if (boardId != "") {
        TopicService.getTopics(boardId, this.pageSize, skip)
          .then((resp) => {
            this.topics = resp.map((item) => this.formJson(item));
          })
          .catch((error) => {
            //
          });
      }
    },

    showDetail(id) {
      this.$router.push({ name: "TopicDetail", params: { id: id } });
    },
    //

    formJson(item) {
      return {
        id: item.id,
        category: item.get("category")
          ? {
              // 适用下拉框的分类
              text: item.get("category").get("title"),
              value: item.get("category").id,
            }
          : "",
        title: item.get("title"),
        content: item.get("content"),
        level: item.get("level"),
        views: item.get("views"),
        reply: item.get("reply"),
        tags: item.get("tags"),
        createdAt: moment(item.createdAt).format("lll"),
        updatedAt: moment(item.updatedAt).format("lll"),
        createdBy: item.get("createdBy")
          ? this.getUser(item.get("createdBy"))
          : "",
        lastEditor: item.get("lastEditor")
          ? this.getUser(item.get("lastEditor"))
          : "",
      };
    },

    // 获取用户表中各个字段的值
    getUser(user) {
      // console.log(user);
      return {
        id: user.id,
        username: user.get("username"),
        email: user.get("email"),
        avatar: user.get("avatar"),
      };
    },
  },
  computed: {
    tableHeight() {
      return this.topics.length * 60;
    },
    //
  },
  watch: {
    //
  },
};
</script>

<style scoped>
.showlink {
  cursor: pointer; /* 显示鼠标箭头 */
  height: 80px;
}
</style>